import React from 'react';
import PropTypes from 'prop-types';
import {
	Panel,
	PanelHeader,
	HeaderButton,
	platform,
	IOS,
	Div,
	View,
	Group,
	Radio,
	Form,
	Card,
	Root,
	Select,
	InfoRow,
	List,
	Cell,
	Link,
	PullToRefresh,
	Button,
	SelectMimicry,
	FormLayout,
	Input,
	Textarea,
	ScreenSpinner
} from '@vkontakte/vkui';
import persik from '../img/persik.png';
import './Persik.css';
import ReactDOM from 'react-dom'
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import Icon24Done from '@vkontakte/icons/dist/24/done';


const osname = platform();

export default class Persik extends React.Component {

	constructor(props){
		super(props);

		this.state={
			title:"",
			text:"",
			mesto:"",
			setcity:"",
			street:"",
			stattime:"",
			owner_id:"",
			chatlink:"",
			type:"",
			grouplink:"",
			agreed_admin:"0",
			view_owner:"1",
			igo:0,metro:"",
			datestop:"",
			timestop:"",
			time:"",
			types:[],
			date:"",
			bookmark:0,
			city:[],
			rr:"ав",
			token:"",
			eid:"",
		}


	}
	componentWillMount() {
		const {your_city,city,title,text,mesto,unixtime,unixtimestop,types,type,chatlink,grouplink,agreed_admin,owner_id,eid,token}=this.props;

		this.setState({token:token,city:city,types:types})







	}

	get(url){
		var xhr = new XMLHttpRequest();
		xhr.open('POST', url, false);
		xhr.send();
		return xhr.responseText;
	}
	post(url,array){
		var xhr = new XMLHttpRequest();
		xhr.open('POST', url, false);
		xhr.setRequestHeader('Content-Type', 'application/x-www-form-urlencoded');
		xhr.send(array);
		return xhr.responseText;
	}
	ret2(item,i){

		return(
			<Cell
				onClick={()=>{

					this.setState({activePanel: "home",

					})



				}}
				data-to="home"
				asideContent={this.state.your_city === item ? <Icon24Done fill="var(--accent)" /> : null}
			>
				{item}
			</Cell>

		);
	}

	Savevvf() {

		const {go,gotovo,startloading,errorcreate} = this.props;
		var startunixtime = new Date(this.state.date+" "+this.state.time).getTime()/1000
		var stopunixtime = new Date(this.state.datestop+" "+this.state.timestop).getTime()/1000

		console.log("title=" + encodeURIComponent(this.state.title) + "&text=" + encodeURIComponent(this.state.text) + "&city=" + encodeURIComponent(this.state.setcity) + "&street=" + encodeURIComponent(this.state.street) +
			"&startdate=" + encodeURIComponent(this.state.date) + "&starttime=" + encodeURIComponent(this.state.time) + "&stopdate=" + encodeURIComponent(this.state.datestop) + "&stoptime" + encodeURIComponent(this.state.timestop) +
			"&type=" + encodeURIComponent(this.state.type) + "&grouplink=" + encodeURIComponent(this.state.grouplink) + "&chatlink=" + encodeURIComponent(this.state.chatlink) + "&view_owner=" + encodeURIComponent(this.state.view_owner) +
			"&agreed_admin=" + encodeURIComponent(this.state.agreed_admin) + "&id=" + this.state.eid+ "&metro=" + encodeURIComponent(this.state.metro))


		var t=this.post("https://api.finebot.site/events/events.create?token=" + this.state.token,
			"title=" + encodeURIComponent(this.state.title) + "&text=" + encodeURIComponent(this.state.text) + "&city=" + encodeURIComponent(this.state.setcity) + "&street=" + encodeURIComponent(this.state.street) + "&starttime=" + encodeURIComponent(startunixtime) + "&stoptime=" + encodeURIComponent(stopunixtime) +
			"&type=" + encodeURIComponent(this.state.type) + "&grouplink=" + encodeURIComponent(this.state.grouplink) + "&chatlink=" + encodeURIComponent(this.state.chatlink) + "&view_owner=" + encodeURIComponent(this.state.view_owner) +
			"&agreed_admin=" + encodeURIComponent(this.state.agreed_admin) + "&id=" + this.state.eid+ "&metro=" + encodeURIComponent(this.state.metro))

		if(t==="{\"response\":1}"){
			gotovo.call()
		}else{
			errorcreate.call()
		}

	}




	getCitys(item,i){
		return(
			<option value={item}>{item}</option>
		);
	}
	getSaveButton(){
		if(this.state.title===""||this.state.text===""||this.state.setcity===""||this.state.street===""||this.state.date===""||this.state.time===""||this.state.datestop===""||this.state.timestop===""||this.state.type===""){
			return(<Button size="l" style={{cursor:"pointer"}} disabled stretched onClick={this.Savevvf.bind(this)}>Сохранить</Button>);
		}else{
			return(<Button size="l" style={{cursor:"pointer"}} stretched onClick={this.Savevvf.bind(this)}>Сохранить</Button>);
		}
	}
	setCity(v){
		var t= document.getElementById("selcity").value
		this.setState({setcity:t})
	}
	setType(v){
		var t= document.getElementById("seltype").value
		this.setState({type:t})
	}
	getTypes(item,i){
		return(
			<option value={item}>{item}</option>
		);
	}
	editTitle(e){
		const { name, value } = e.currentTarget;
		this.setState({title:value})
	}
	editText(e){
		const { name, value } = e.currentTarget;
		this.setState({text: value})
	}
	editStreet(e){
		const { name, value } = e.currentTarget;
		this.setState({street:value})
	}
	editStartTime(e){
		const { name, value } = e.currentTarget;
		this.setState({time:value})
	}
	editStartDate(e){
		const { name, value } = e.currentTarget;
		this.setState({date:value})
	}
	editStopTime(e){
		const { name, value } = e.currentTarget;
		this.setState({timestop:value})
	}
	editStopDate(e){
		const { name, value } = e.currentTarget;
		this.setState({datestop:value})
	}
	editGroupLink(e){
		const { name, value } = e.currentTarget;
		this.setState({grouplink:value})
	}
	editChatLink(e){
		const { name, value } = e.currentTarget;
		this.setState({chatlink:value})
	}
	editMetro(e){
		const { name, value } = e.currentTarget;
		this.setState({metro:value})
	}
	render() {

		var one=<Radio name="radio" onClick={()=>this.setState({agreed_admin:"0"})} value="1">Не согласовано</Radio>
		var two=<Radio name="radio" onClick={()=>this.setState({agreed_admin:"null"})} value="2">Не требуется</Radio>
		var three=<Radio name="radio" onClick={()=>this.setState({agreed_admin:"1"})} value="3">Согласовано</Radio>

		switch (this.state.agreed_admin) {
			case "1":
				three=<Radio name="radio" onClick={()=>this.setState({agreed_admin:"1"})} defaultChecked value="3">Согласовано</Radio>
				break;
			case "0":
				one=<Radio name="radio" onClick={()=>this.setState({agreed_admin:"0"})} defaultChecked value="1">Не согласовано</Radio>
				break;
			default:
				two=<Radio name="radio" onClick={()=>this.setState({agreed_admin:"null"})} defaultChecked value="2">Не требуется</Radio>
				break;
		}

		var yes=<Radio name="radio" onClick={()=>this.setState({view_owner:1})} value="1">Да</Radio>
		var no= <Radio name="radio" onClick={()=>this.setState({view_owner:0})} value="2">Нет</Radio>
		if(this.state.view_owner==1){
			yes=<Radio name="radio" onClick={()=>this.setState({view_owner:1})} defaultChecked value="1">Да</Radio>
		}else{
			no= <Radio name="radio" onClick={()=>this.setState({view_owner:0})} defaultChecked value="2">Нет</Radio>
		}

		const {go}=this.props;

		return (


			<Panel id="create">
				<PanelHeader
					left={<HeaderButton style={{cursor:"pointer"}} onClick={()=>window.history.back()} data-to="my">{osname === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}</HeaderButton>}>
					Создание события
				</PanelHeader>

				<Group>
					<FormLayout><Input id="title"

									   onChange={this.editTitle.bind(this)} defaultValue={this.state.title} top="Заголовок *"></Input></FormLayout>
				</Group>
				<Group>
					<FormLayout>
						<Textarea id="prim" onChange={this.editText.bind(this)}

								  defaultValue={this.state.text} top="Примечание *" placeholder="Место, подробности" />
					</FormLayout>
				</Group>
				<Group>
					<FormLayout>
						<Select
							top="Город *"
							onChange={()=>this.setCity()}
							id="selcity"
							data-to="other"

							placeholder="Не выбран"
							defaultValue={this.state.setcity}
						>{this.state.your_city}
							{this.state.city.map(this.getCitys)}
						</Select>
					</FormLayout>
					<FormLayout><Input top="Улица *"

									   onChange={this.editStreet.bind(this)} defaultValue={this.state.street}></Input>
						<Input type="text" defaultValue={this.state.metro}

							   top="Ближайшее метро" onChange={this.editMetro.bind(this)}/></FormLayout>

				</Group>


				<Group>
					<FormLayout>
						<Input type="date" defaultValue={this.state.date}
							   max={this.state.datestop}

							   top="Дата начала *" onChange={this.editStartDate.bind(this)}/>
						<Input type="time"

							   defaultValue={this.state.time} onChange={this.editStartTime.bind(this)} top="Время начала *"/>
					</FormLayout>
				</Group>
				<Group>
					<FormLayout>
						<Input type="date" defaultValue={this.state.datestop}
							   min={this.state.date}

							   onChange={this.editStopDate.bind(this)} top="Дата окончания *"/>
						<Input type="time" defaultValue={this.state.timestop}

							   onChange={this.editStopTime.bind(this)} top="Время окончания *"/>
					</FormLayout>
				</Group>
				<Group>
					<FormLayout>
						<Select
							top="Тип *"
							id="seltype"
							data-to="other"

							onChange={()=>this.setType()}
							placeholder="Не выбран"
							defaultValue={this.state.type}
						>{this.state.your_city}
							{this.state.types.map(this.getTypes)}
						</Select>
					</FormLayout>
				</Group>
				<Group>
					<FormLayout><Input defaultValue={this.state.grouplink} top="Ссылка на группу" onChange={this.editGroupLink.bind(this)}></Input></FormLayout>
				</Group>
				<Group>
					<FormLayout><Input defaultValue={this.state.chatlink} top="Ссылка на беседу" onChange={this.editChatLink.bind(this)}></Input></FormLayout>
				</Group>
				<Group>
					<FormLayout>
						<div top="Показывать организатора?">
							{yes}
							{no}

						</div>
					</FormLayout>
				</Group>
				<Group>
					<FormLayout>
						<div top="Согласованность с администрацией города" >
							{one}
							{two}
							{three}

						</div>
					</FormLayout>
				</Group>
				<Div style={{display: 'flex'}}>
					{this.getSaveButton()}
				</Div>
			</Panel>

		);
	}
}
const style={
	fd: {

		width: '100%',
		display: 'flex',
		height: '100%',
		justifyContent: 'bottom',


	},
	buttons:{
		height: '100%',
		padding: '3px',
		width: '100%',


	},
};
Persik.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};


